import "../style/main.scss";
// import "material-components-web";

import React = require("react");
import { FormEvent, ReactNode } from "react";
import { render } from "react-dom";
import { connect, Provider } from "react-redux";
import { Dispatch, compose } from "redux";
import {
    makeStore, State, changePassword, computePassword,
    changeRange, changeSize, ActionType
} from "./store";
import { Range } from "./password";
import Clipboard = require("clipboard");

interface PasswordProps {
    password: string;
    passwordId: string;
    output: string;
    outputSize: number;
    changePassword: (password: string, id: string) => any;
    entropy: number;
    ranges: Range[];
    selectedRange: Range;
    selectRange: (range: Range) => any;
    sizeLimit?: number;
    changeSize: (size: number) => any;
    showPassword: boolean;
    showHidePassword: () => any;
}

function Input(label: string, value: string, onChange: (e: FormEvent<HTMLInputElement>) => any, type?: string) {
    const typ = type ? type : "text";
    return [
        <div className="label">{label}:</div>,
        <input type={typ} value={value} onChange={onChange} className="data" />
    ];
};


function eventValue(event: FormEvent<HTMLInputElement> | FormEvent<HTMLSelectElement>) {
    return event.currentTarget.value;
}

interface GenericSelectProps<T> {
    values: T[];
    value?: T;
    onChange: (newVal: T) => any;
    toString: (value: T) => string;
    className?: string;
}
function GenericSelect<T>(props: GenericSelectProps<T>) {
    const options = props.values.map(value => {
        const str = props.toString(value);
        return <option key={str} value={str}>{str}</option>;
    });
    const selected = props.value ? props.toString(props.value) : undefined;
    const lookupValue = (name: string): T => props.values.find(value => props.toString(value) === name)!;
    const onChange = compose(props.onChange, lookupValue, eventValue);
    return <select className={props.className} value={selected} onChange={onChange}>{options}</select>;
}

function OutputDisplay(name: string, value: ReactNode) {
    return [
        <div className="label">{name}:</div>,
        <div className="data">{value}</div>
    ];
}

new Clipboard("#copy-password");
function PasswordHasher(props: PasswordProps) {
    const passChange = compose(password => props.changePassword(password, props.passwordId), eventValue);
    const idChange = compose(id => props.changePassword(props.password, id), eventValue);
    const sizeChange = compose(props.changeSize, parseInt, eventValue);
    const selectProps: GenericSelectProps<Range> = {
        values: props.ranges,
        value: props.selectedRange,
        onChange: props.selectRange,
        toString: range => range.name!
    };
    const sizeLimit = props.sizeLimit ? props.sizeLimit.toString() : "";
    const password = props.showPassword ? props.output : props.output.replace(/./g, "*");
    const passMess = props.showPassword ? "Hide password" : "Show password";
    return (
        <div id="hasher">
            {Input("Password", props.password, passChange, "password")}
            {Input("Identifier", props.passwordId, idChange)}
            {Input("Size Limit", sizeLimit, sizeChange, "number")}
            <div className="label">Output Type:</div>
            <GenericSelect className="data" {...selectProps} />
            <div id="output">
                {OutputDisplay("Output", password)}
                <button data-clipboard-text={props.output} id="copy-password">Copy to clipboard</button>
                <button onClick={props.showHidePassword}>{passMess}</button>
                {OutputDisplay("Output Size", props.outputSize)}
                {OutputDisplay("Entropy", props.entropy)}
            </div>
        </div>
    );
}

function mapStateToProps(state: State) {
    const [output, entropy] = computePassword(state);
    return {
        ...state,
        entropy,
        output: output.join(""),
        outputSize: output.length
    };
}

function mapDispatchToProps(dispatch: Dispatch<State>) {
    return {
        changePassword: compose(dispatch, changePassword),
        selectRange: compose(dispatch, changeRange),
        changeSize: compose(dispatch, changeSize),
        showHidePassword: () => dispatch({ type: ActionType.DisplayPassword })
    };
}

makeStore().then(store => {
    const Index = connect(mapStateToProps, mapDispatchToProps)(PasswordHasher);
    render(<Provider store={store}><Index /></Provider>, document.getElementById("app"));
});



